export { switchBtn, train, play, categoryInfo };
export { switchToggle };
import { menuBg } from './menu.js';

const switchBtn = document.querySelector('.checkbox');
const train = document.querySelector('.train');
const play = document.querySelector('.play');
const categoryInfo = document.querySelectorAll('.category__info');



function switchToggle() {
    if (!train.classList.contains('none')) {
        train.classList.add('none');
        menuBg.classList.add('menu-bg--play');
        play.classList.remove('none');
        for (let i = 0; i < categoryInfo.length; i++) {
            categoryInfo[i].classList.add('info--play');
        }
    } else {
        train.classList.remove('none');
        menuBg.classList.remove('menu-bg--play');
        play.classList.add('none');
        for (let i = 0; i < categoryInfo.length; i++) {
            categoryInfo[i].classList.remove('info--play');
        }
    }
}
